<template>
  <div>
    <h1 class="mb-6 mt-2 font-bold text-lg md:text-2xl">
      <Link class="text-gray-600 hover:text-gray-800 inline-flex items-center" :href="route('services')">
        <Icons name="back" class="flex-shrink-0 w-3 h-3 md:w-5 md:h-5 fill-current ltr:mr-2 rtl:ml-2 rtl:rotate" /> {{ $tc('Service', 2) }}
      </Link>
      <span class="text-gray-400 font-medium">/</span>
      {{ form.name }}
    </h1>
    <trashed-message v-if="service.deleted_at" class="mb-6" @restore="restore">
      <span>{{ $t('already_deleted_x', { x: $tc('Service') }) }}</span>
    </trashed-message>
    <div class="bg-white rounded shadow overflow-hidden max-w-3xl">
      <form @submit.prevent="submit" autocomplete="off">
        <div class="p-8 ltr:-mr-6 rtl:-ml-6 ltr:ml-0 rtl:mr-0 -mb-8 flex flex-wrap">
          <text-input
            v-model="form.code"
            :errors="$page.props.errors?.code"
            class="ltr:pr-6 rtl:pl-6 pb-8 w-full lg:w-1/2"
            :label="$t('Code')"
          />
          <text-input
            v-model="form.name"
            :errors="$page.props.errors?.name"
            class="ltr:pr-6 rtl:pl-6 pb-8 w-full lg:w-1/2"
            :label="$t('Name')"
          />
        </div>
        <div class="px-8 pb-8 ltr:-mr-6 rtl:-ml-6 ltr:ml-0 rtl:mr-0 -mb-8 flex flex-wrap">
          <text-input v-model="form.price" :errors="$page.props.errors?.price" class="ltr:pr-6 rtl:pl-6 pb-8 w-full" :label="$t('Price')" />
          <text-input
            v-model="form.description"
            :errors="$page.props.errors?.description"
            class="ltr:pr-6 rtl:pl-6 pb-8 w-full"
            :label="$t('Description')"
          />
          <checkbox-input id="active" :label="$t('Active')" v-model="form.active" :checked="form.active" class="mb-8"></checkbox-input>
        </div>
        <div class="px-8 py-4 bg-gray-100 border-t border-gray-200 flex items-center">
          <button v-if="!service.deleted_at" class="text-red-600 hover:underline" tabindex="-1" type="button" @click="destroy">
            {{ $t('delete_x', { x: $tc('Service') }) }}
          </button>
          <button v-else class="text-red-600 hover:underline" tabindex="-1" type="button" @click="destroyPermanently">
            {{ $t('Delete Permanently') }}
          </button>
          <loading-button :loading="sending" class="btn-gray ltr:ml-auto rtl:mr-auto" type="submit">
            {{ $t('update_x', { x: $tc('Service') }) }}
          </loading-button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import Layout from '@/Shared/Layout';
import TextInput from '@/Shared/TextInput';
import DeleteMixin from '@/Misc/DeleteMixin';
import LoadingButton from '@/Shared/LoadingButton';
import CheckboxInput from '@/Shared/CheckboxInput';
import TrashedMessage from '@/Shared/TrashedMessage';

export default {
  layout: Layout,
  remember: 'form',
  mixins: [DeleteMixin('service')],
  props: { service: Object, customers: Array },
  components: { LoadingButton, TextInput, TrashedMessage, CheckboxInput },
  metaInfo() {
    return {
      title: this.$t('update_x', { x: this.form.name }),
    };
  },
  data() {
    return {
      sending: false,
      form: {
        code: this.service.code,
        name: this.service.name,
        price: this.service.price,
        description: this.service.description,
        active: this.service.active == 1 ? true : false,
      },
    };
  },
  methods: {
    submit() {
      this.sending = true;
      this.$inertia.put(this.route('services.update', this.service.id), this.form).then(() => (this.sending = false));
    },
  },
};
</script>
