import { createI18n } from 'vue-i18n';

import en from '@lang/en.json';
import es from '@lang/es.json';

const messages = { en, es };

const i18n = createI18n({
  messages,
  missingWarn: false,
  fallbackWarn: false,
  fallbackLocale: 'en',
  warnHtmlMessage: false,
  locale: window.Locale || 'en',
});

export default i18n;

const languages = require('@lang/lang.json');

Object.keys(languages.available).map(i => {
  messages[languages.available[i].value] = require(`@lang/${languages.available[i].value}.json`);
});

export { languages };
