<template>
  <div class="w-full h-full flex items-center p-6">
    <div class="w-full max-w-sm mx-auto bg-white rounded shadow-lg leading-normal p-4">
      <h4 class="font-bold">{{ $t('Send SMS to Customer') }}</h4>
      <h4 class="text-sm mt-2 mb-4" v-html="$t('sending_to_html', { name: order.customer.name, phone: order.customer.phone })"></h4>
      <textarea-input v-model="text" :errors="errors.text" class="pb-10 w-full" label="SMS Text" />
      <div class="-m-4 px-8 py-4 bg-gray-100 border-t border-gray-200 flex items-center rounded-b">
        <button class="text-gray-700 hover:text-gray-800" type="button" @click="$modal.hide('sms-modal')">
          {{ $t('Cancel') }}
        </button>
        <button @click="send" :disabled="sending" class="btn-gray px-4 py-2 ml-auto flex items-center">
          <div v-if="sending" class="btn-spinner ltr:mr-2 rtl:ml-2" />
          {{ $t('Send') }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import TextareaInput from '@/Shared/TextareaInput';
export default {
  components: { TextareaInput },
  props: { order: Object },
  data() {
    return { text: null, sending: false, errors: {} };
  },
  methods: {
    send() {
      this.sending = true;
      this.$axios
        .post(this.route('customers.sms', this.order.customer.id).url(), { text: this.text })
        .then(res => {
          this.sending = false;
          if (res.data.success) {
            this.$modal.hide('sms-modal');
          }
          this.$modal.show('dialog', {
            title: res.data.success ? this.$t('SMS Sent') : this.$t('Error Occurred'),
            text: res.data.success
              ? this.$t('SMS has been successfully sent to customer.')
              : res.data.error || this.$t('System is unable to send sms, please check logs in storage directory.'),
            buttons: [
              {
                title: this.$t('Okay'),
                class: 'px-6 py-3 border-l bg-gray-200 hover:bg-gray-400',
              },
            ],
          });
        })
        .catch(err => {
          this.sending = false;
          this.errors = err.response.data.errors;
        });
    },
  },
};
</script>
