<template>
  <span class="checkbox ltr:mr-2 rtl:ml-3">
    <input
      :id="id"
      :value="value"
      type="checkbox"
      class="invisible"
      :checked="checked"
      @change="$emit('input', $event.target.checked && v > 1 ? v : $event.target.checked)"
    />
    <label :for="id" v-if="label" :class="{ 'text-red-600': errors.length }">
      <span class="ltr:mr-2 rtl:ml-2" :class="{ 'border-red-600': errors.length }"></span> {{ label }}
    </label>
  </span>
</template>

<script>
export default {
  props: {
    label: {
      type: String,
      required: false,
    },
    value: {
      required: false,
    },
    id: {
      type: String,
      required: false,
    },
    errors: {
      type: Array,
      default: () => [],
    },
    checked: {
      type: Boolean,
      required: false,
    },
  },
  data() {
    return { v: this.value };
  },
};
</script>
