<template>
  <div>
    <h1 class="mb-6 mt-2 font-bold text-lg md:text-2xl">{{ $tc('Payment', 2) }}</h1>
    <div class="np mb-6 flex justify-between items-center">
      <search-filter v-model="form.search" class="w-full max-w-md ltr:mr-4 rtl:ml-4" @reset="reset">
        <label class="block text-gray-700">{{ $t('Status') }}</label>
        <select v-model="form.received" class="mt-1 mb-6 w-full form-select ltr:arrow-right rtl:arrow-left">
          <option :value="null">{{ $t('All') }}</option>
          <option value="yes">{{ $t('Received') }}</option>
          <option value="no">{{ $t('Not received yet') }}</option>
        </select>
        <label class="block text-gray-700">{{ $t('Gateway') }}</label>
        <select v-model="form.gateway" class="mt-1 mb-6 w-full form-select ltr:arrow-right rtl:arrow-left">
          <option :value="null">{{ $t('All') }}</option>
          <option v-for="g in gateways" :value="g" :key="g">{{ g }}</option>
        </select>
        <label class="block text-gray-700">{{ $t('Trashed') }}:</label>
        <select v-model="form.trashed" class="mt-1 w-full form-select ltr:arrow-right rtl:arrow-left">
          <option :value="null"></option>
          <option value="with">{{ $t('With Trashed') }}</option>
          <option value="only">{{ $t('Only Trashed') }}</option>
        </select>
      </search-filter>
      <Link class="btn-gray" :href="route('payments.new')">
        <span class="inline md:hidden"><Icons name="add" class="w-4 h-4 fill-white group-hover:fill-white" /></span>
        <span class="hidden md:inline">{{ $t('create_x', { x: $tc('Payment') }) }}</span>
      </Link>
    </div>
    <div class="bg-white rounded shadow leading-normal overflow-x-auto scroll-on-light">
      <table class="w-full whitespace-no-wrap">
        <thead>
          <tr class="text-left font-bold">
            <th class="px-6 pt-6 pb-4">{{ $t('Created at') }}</th>
            <th class="px-6 pt-6 pb-4">{{ $t('Customer') }}</th>
            <th class="px-6 pt-6 pb-4">{{ $t('Amount') }}</th>
            <!-- <th class="px-6 pt-6 pb-4">Gateway</th> -->
            <th class="px-6 pt-6 pb-4" colspan="2">{{ $t('Description') }}</th>
          </tr>
        </thead>
        <tbody>
          <tr
            :key="payment.id"
            @click="rowClicked(payment.id)"
            v-for="payment in payments.data"
            class="cursor-pointer hover:bg-teal-200 focus-within:bg-teal-200"
          >
            <td class="border-t px-6 py-4">
              <div class="flex items-center">
                {{ payment.date | date($root.$data.lang.locale) }}
                <Icons v-if="payment.deleted_at" name="trash" class="flex-shrink-0 w-3 h-3 fill-gray-400 ml-2" />
                <br />
                {{ payment.reference | reference }}
              </div>
            </td>
            <td class="border-t px-6 py-4">
              <div v-if="payment.customer">
                {{ payment.customer.name }}
                <div>{{ payment.customer.phone }}</div>
              </div>
            </td>
            <td class="border-t px-6 py-4">
              <div class="ltr:mr-2 rtl:ml-2">{{ payment.gateway }}</div>
              <div class="text-right font-bold flex items-center justify-end">
                <Icons v-if="payment.received" name="check" class="flex-shrink-0 w-3 h-3 fill-green-600 ltr:mr-2 rtl:ml-2" />
                {{ payment.amount | formatNumber($root.$data.lang.locale) }}
              </div>
            </td>
            <!-- <td class="border-t px-6 py-4 text-center">
              {{ payment.gateway }}
            </td> -->
            <td class="border-t px-6 py-4 max-w-lg truncate">
              {{ payment.description }}
            </td>
            <td class="border-t px-6 py-4 w-8">
              <Icons name="cheveron-right" class="block w-6 h-6 fill-gray-400 rtl:rotate" />
            </td>
          </tr>
          <tr v-if="payments.data.length === 0">
            <td class="border-t px-6 py-4" colspan="5">{{ $t('no_x_found', { x: $tc('Payment') }) }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <pagination :links="payments.links" />
  </div>
</template>

<script>
import pickBy from 'lodash/pickBy';
import Layout from '@/Shared/Layout';
import throttle from 'lodash/throttle';
import Show from '@/Pages/Payments/Show';
import mapValues from 'lodash/mapValues';
import Pagination from '@/Shared/Pagination';
import SearchFilter from '@/Shared/SearchFilter';

export default {
  layout: Layout,
  components: { Pagination, SearchFilter },
  props: { payments: Object, filters: Object, message: String },
  metaInfo() {
    return { title: this.$tc('Payment', 2) };
  },
  data() {
    return {
      form: {
        search: this.filters.search,
        gateway: this.filters.gateway,
        trashed: this.filters.trashed,
        received: this.filters.received,
      },
      gateways: [],
      payment: null,
    };
  },
  watch: {
    form: {
      handler: throttle(function () {
        let query = pickBy(this.form);
        this.$inertia.replace(this.route('payments', Object.keys(query).length ? query : { remember: 'forget' }));
      }, 250),
      deep: true,
    },
  },
  created() {
    this.$set(this.$page.props.flash, 'success', this.message);
    this.gateways = this.$page.props.auth.user.account.gateways.split(',');
  },
  methods: {
    rowClicked(n) {
      // this.$inertia.visit(this.route('payments.edit', n));
      // this.$event.fire('loading', true);
      fetch(this.route('payments.ajax', n)).then(res => {
        res.json().then(data => {
          // this.$event.fire('loading', false);
          this.payment = data;
          this.$modal.show(
            Show,
            { payment: this.payment },
            { name: 'payment-modal', height: '100%', transition: 'animate zoomIn', width: '100%' },
            {
              'before-open': () => {
                document.title = this.$tc('Payment Number', { x: this.payment.id });
                document.querySelector('body').classList.add('modal-open');
              },
              'before-close': () => {
                document.title = this.$tc('Payment', 2);
                document.querySelector('body').classList.remove('modal-open');
              },
            }
          );
        });
      });
    },
    reset() {
      this.form = mapValues(this.form, () => null);
    },
  },
};
</script>
